
import {defineComponent, PropType} from 'vue';
import {IApiImageData} from "@/global/api"

export default defineComponent({
  name: 'WatchComponent',

  components: {},

  props: {
    data: {
      type: Object as PropType<IApiImageData>,
      required: true,
    }
  },

  computed: {
    name(): string {
      return this.data.safeName.replace( 'nc-level-one-' , '')
    }
  }
});
